import React, { Component } from 'react';
import { translate } from 'react-i18next';
import DateRange from '@material-ui/icons/DateRange';
import Animated from './Animated';
import { toSnakeCase } from './Events';
export class Education extends Component {
  componentDidMount() {
    this.props.getEducationRequest();
  }

  render() {
    const { t, data } = this.props;

    return (
      data && (
        <Animated>
          {data.map(
            (item, key) =>
              item && (
                <div key={key} className="block">
                  <p>
                    <b>{t(`Education:${toSnakeCase(item.name)}`, item.name)}</b>{' '}
                    | <b>{t('degree', 'Degree')}:</b>{' '}
                    {t(`Education:${toSnakeCase(item.degree)}`, item.degree)}
                  </p>
                  <p />
                  <p className="date">
                    <span>
                      <DateRange />{' '}
                      <span>
                        {t(`Education:${toSnakeCase(item.date)}`, item.date)}
                      </span>
                    </span>
                  </p>
                  <p>
                    {t(`Education:${toSnakeCase(item.faculty)}`, item.faculty)}
                  </p>
                </div>
              )
          )}
        </Animated>
      )
    );
  }
}

export default translate(['UI', 'Education'])(Education);



